import { useState } from 'react'
import { useNavigate, useParams } from 'react-router'

import { Box, Button, Card, CardHeader, Divider, Typography, useMediaQuery, useTheme } from '@mui/material'
import type { GridCellParams, GridColDef, GridEventListener, GridRowSelectionModel } from '@mui/x-data-grid'
import { DataGrid } from '@mui/x-data-grid'

import { FIRST_SEMESTER } from 'src/data/semester'

import type { RecruitList } from 'src/models/recruit'

import { useGetRecruitList } from 'src/hooks/api/recruit'

import BulkActions from './BulkActions'
import DateSelectionDialog from './DateSelectionDialog'

interface RecruitTableProps {
  handleDelete(ids: number[]): void
}

const RecruitTable: React.FC<RecruitTableProps> = ({ handleDelete }) => {
  const theme = useTheme()
  const matches = useMediaQuery(theme.breakpoints.up('sm'))
  const navigate = useNavigate()
  const { id } = useParams()
  const clubId = Number(id)

  const [isDialog, setIsDialog] = useState<boolean>(false)
  const [selectedRecruits, setSelectedRecruits] = useState<GridRowSelectionModel>([])

  const recruitList = useGetRecruitList(clubId)

  const selectedBulkActions = selectedRecruits.length > 0

  const recruitListRows =
    recruitList.data?.map(recruit => ({
      ...recruit,
      date:
        recruit.semester === FIRST_SEMESTER
          ? recruit.year
            ? `${recruit.year.toString().slice(-2)}-1`
            : ''
          : recruit.year
          ? `${recruit.year.toString().slice(-2)}-2`
          : '',
      recruitDate: `${new Date(recruit.startAt).toLocaleDateString()} ~ ${
        recruit.endAt ? new Date(recruit.endAt).toLocaleDateString() : '상시 모집'
      }`,
      applyAllCount: recruit.applyAllCount ? `${recruit.applyAllCount}명` : '0명',
      applyPassCount: recruit.applyPassCount ? `${recruit.applyPassCount}명` : '0명',
      applyWaitCount: recruit.applyWaitCount ? `${recruit.applyWaitCount}명` : '0명'
    })) || []

  const columns: GridColDef[] = [
    { field: 'date', headerName: '학기', maxWidth: 100, flex: 1, headerAlign: 'center', align: 'center' },
    { field: 'title', headerName: '제목', maxWidth: 400, flex: 1 },
    { field: 'recruitDate', headerName: '모집 기간', maxWidth: 250, flex: 1 },
    { field: 'applyAllCount', headerName: '지원자 수', maxWidth: 100, flex: 1, headerAlign: 'center', align: 'center' },
    {
      field: 'applyPassCount',
      headerName: '합격자 수',
      maxWidth: 100,
      flex: 1,
      headerAlign: 'center',
      align: 'center'
    },
    {
      field: 'applyWaitCount',
      headerName: '대기자 수',
      maxWidth: 100,
      flex: 1,
      headerAlign: 'center',
      align: 'center'
    },
    {
      field: 'form',
      headerName: '',
      flex: 1,
      maxWidth: 150,
      sortable: false,
      filterable: false,
      renderCell: () => {
        return <Button fullWidth>신청폼 관리</Button>
      }
    },
    {
      field: 'applicant',
      headerName: '',
      flex: 1,
      maxWidth: 150,
      sortable: false,
      filterable: false,
      renderCell: () => {
        return <Button fullWidth>지원자 관리</Button>
      }
    }
  ]
  const columnsForXS: GridColDef[] = [
    { field: 'date', headerName: '학기', maxWidth: 100, flex: 1 },
    { field: 'recruitDate', headerName: '모집 기간', maxWidth: 200, flex: 1 },
    { field: 'applyAllCount', headerName: '지원자 수', maxWidth: 100, flex: 1 }
  ]

  const handleCellClick: GridEventListener<'cellClick'> = params => {
    const recruitId = params.row.id
    if (params.field === 'form') {
      navigate(`/manage/${clubId}/recruit/${recruitId}/form`)
      return
    }
    if (params.field === 'applicant') {
      navigate(`/manage/${clubId}/recruit/${recruitId}/applicants`)
      return
    }
    if (params.field === 'title') {
      navigate(`/manage/${clubId}/recruit/${recruitId}`)
      return
    }
  }

  const handleAddRecruit = () => {
    setIsDialog(true)
  }

  return (
    <>
      <Card>
        <Box height={matches || selectedBulkActions ? '55px' : '110px'}>
          {selectedBulkActions ? (
            <Box flex={1} p={1}>
              <BulkActions selectedRecruits={selectedRecruits as number[]} handleDelete={handleDelete} />
            </Box>
          ) : (
            <Box
              display="flex"
              alignItems="baseline"
              flexDirection={matches ? 'row' : 'column'}
              justifyContent="space-between"
            >
              <Box display={'flex'} alignItems={'baseline'}>
                <CardHeader title="모집공고 목록" />
                <Typography variant={'caption'}>공고 수정은 제목을 클릭해주세요</Typography>
              </Box>
              <Button
                onClick={handleAddRecruit}
                variant="contained"
                sx={{
                  marginRight: matches ? 1.5 : 1,
                  marginLeft: matches ? 0 : 1,
                  marginBottom: matches ? 0 : 1,
                  width: matches ? 'auto' : '95%'
                }}
              >
                모집공고 추가
              </Button>
            </Box>
          )}
        </Box>
        <Divider />
        <Box
          sx={{
            '& .title--bold': {
              fontWeight: 'bold',
              textDecoration: 'underline',
              cursor: 'pointer'
            }
          }}
        >
          <DataGrid
            autoHeight
            rows={recruitListRows}
            columns={matches ? columns : columnsForXS}
            initialState={{
              sorting: { sortModel: [{ field: 'recruitDate', sort: 'desc' }] },
              pagination: { paginationModel: { pageSize: window.innerWidth >= 600 ? 10 : 5 } }
            }}
            pageSizeOptions={[5, 10, 25]}
            checkboxSelection
            onRowSelectionModelChange={newSelectionModel => {
              setSelectedRecruits(newSelectionModel)
            }}
            rowSelectionModel={selectedRecruits}
            onCellClick={handleCellClick}
            getCellClassName={(params: GridCellParams<RecruitList>) => {
              if (params.field === 'title') {
                return 'title--bold'
              } else return ''
            }}
            disableRowSelectionOnClick
          />
        </Box>
      </Card>
      <DateSelectionDialog
        open={isDialog}
        handleClose={() => {
          setIsDialog(false)
        }}
      />
    </>
  )
}

export default RecruitTable
