import { useSnackbar } from 'notistack'
import { useEffect, useState } from 'react'
import { useQueryClient } from 'react-query'
import { useParams } from 'react-router'
import { useRecoilState } from 'recoil'

import type { SelectChangeEvent } from '@mui/material'
import {
  Box,
  Button,
  Dialog,
  FormControl,
  InputLabel,
  MenuItem,
  Select,
  Stack,
  Typography,
  useMediaQuery,
  useTheme
} from '@mui/material'

import { DATE_MESSAGE } from 'src/data/messages'
import { FIRST_SEMESTER, SECOND_SEMESTER } from 'src/data/semester'

import type { DateProps, Semester } from 'src/models/date'
import { defaultDate, isSemester, isYear } from 'src/models/date'

import { useGetRecruitExistence } from 'src/hooks/api/recruit'
import useSafeAuthUser from 'src/hooks/useSafeAuthUser'
import { clubDateState, recruitDateState } from 'src/recoil/atom'

interface DateSelectionDialogProps {
  open: boolean
  handleClose: React.MouseEventHandler<HTMLButtonElement>
}

const DateSelectionDialog: React.FC<DateSelectionDialogProps> = ({ open, handleClose }) => {
  const { userInfo } = useSafeAuthUser()
  const { id } = useParams()
  const clubId = Number(id)
  const club = userInfo?.clubs.find(club => club.id === clubId)

  const [recruitDate, setRecruitDate] = useRecoilState(recruitDateState)
  const [clubDate, setClubDate] = useRecoilState(clubDateState)
  const [isSubmit, setIsSubmit] = useState(false)

  useEffect(() => {
    const newDate: DateProps = {
      year: club?.year || defaultDate.year,
      semester: club?.semester || defaultDate.semester
    }

    if (newDate.year !== clubDate.year || newDate.semester !== clubDate.semester) {
      setRecruitDate(newDate)
      setClubDate(newDate)
    }
  }, [])

  const queryClient = useQueryClient()
  const { isSuccess } = useGetRecruitExistence(clubId, recruitDate, isSubmit)

  useEffect(() => {
    setIsSubmit(false)
  }, [isSuccess])

  const { enqueueSnackbar } = useSnackbar()
  const theme = useTheme()
  const isUpperThanSm = useMediaQuery(theme.breakpoints.up('sm'))
  const { year, semester } = recruitDate

  const handleDateChange = ({ target: { value } }: React.ChangeEvent<HTMLInputElement> | SelectChangeEvent<string>) => {
    if (isSemester(value)) {
      setRecruitDate(prevDate => ({ ...prevDate, semester: value as Semester }))
      return
    }
    if (isYear(value)) {
      setRecruitDate(prevDate => ({ ...prevDate, year: Number(value) }))
      return
    }
    enqueueSnackbar(DATE_MESSAGE.INVALID_DATE, { variant: 'warning' })
  }

  const handleSubmit = () => {
    setIsSubmit(true)
    queryClient.invalidateQueries(['recruitExistence'])
  }

  return (
    <Dialog open={open} onClose={handleClose}>
      <Box textAlign="center" p={3}>
        <Typography variant="h4" color="primary">
          새로 공고를 등록할 학기를 선택해주세요
        </Typography>
        <Box mt={3}>
          <Stack
            width="100%"
            direction={isUpperThanSm ? 'row' : 'column'}
            spacing={1}
            alignItems="center"
            justifyContent={isUpperThanSm ? 'flex-end' : 'center'}
            sx={{ marginBottom: 3 }}
          >
            <FormControl size="small">
              <InputLabel id="year-select-label">년도</InputLabel>
              <Select
                labelId="year-select-label"
                id="year-select"
                name="year"
                value={year as unknown as string}
                label="년도"
                onChange={handleDateChange}
                sx={{ width: isUpperThanSm ? '180px' : '40vw' }}
              >
                <MenuItem value={clubDate.year}>{clubDate.year}</MenuItem>
                <MenuItem value={clubDate.year + 1}>{clubDate.year + 1}</MenuItem>
              </Select>
            </FormControl>
            <FormControl size="small">
              <InputLabel id="semester-select-label">학기</InputLabel>
              <Select
                labelId="semester-select-label"
                id="semester-select"
                name="semester"
                value={semester}
                label="학기"
                onChange={handleDateChange}
                sx={{ width: isUpperThanSm ? '180px' : '40vw' }}
              >
                <MenuItem value={FIRST_SEMESTER}>1학기</MenuItem>
                <MenuItem value={SECOND_SEMESTER}>2학기</MenuItem>
              </Select>
            </FormControl>
          </Stack>
        </Box>
        <Box display="flex" justifyContent="space-evenly" mt={2}>
          <Button variant="outlined" onClick={handleClose}>
            취소
          </Button>
          <Button variant="contained" onClick={handleSubmit}>
            확인
          </Button>
        </Box>
      </Box>
    </Dialog>
  )
}

export default DateSelectionDialog
